body {
  font-family: "Roboto", sans-serif;
  background-color: #eceff1;
}

.show-data {
  width: 100%;
  background-color: #cfd8dc;
  margin: 20px;
  padding: 10px;
  border-radius: 6px;
  box-shadow: 10px 5px 5px grey;
}

h1 {
  text-align: center;
}

h1.show-data {
  text-align: center;
}

image.show-data {
  display: block;
}

.show-info {
  width: 100%;
}

@media (min-width: 800px) {
  .show-data {
    display: flex;
  }
  .show-info {
    padding: 10px;
  }
}

@media (max-width: 799px) {
  image {
    margin: auto;
  }
  p.show-info {
    text-align: center;
    padding: 10px;
  }
}
